.header__theme-switcher {
  cursor: pointer;
  width: 38px;
  height: 16px;
  background-color: var(--accent-color);
  background: var(--linear-gradient-down);
  border-radius: 194px;
  align-items: center;
  gap: 14px;
  padding: 5px;
  display: flex;
  position: relative;
  top: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher {
    width: 54px;
    height: 20px;
    gap: 24px;
    margin-left: auto;
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher {
    width: 60px;
    height: 24px;
    gap: 24px;
  }
}

.header__theme-switcher:before {
  content: "";
  background-color: var(--second-color);
  z-index: 2;
  width: 12px;
  height: 12px;
  transform: translateX(var(--switcher-pos-mob) );
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12.5%;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:before {
    width: 16px;
    height: 16px;
    transform: translateX(var(--switcher-pos-tab) );
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher:before {
    width: 18px;
    height: 18px;
    transform: translateX(var(--switcher-pos-desk) );
  }
}

.header__theme-switcher:after {
  content: "";
  z-index: 2;
  border-style: solid;
  border-width: 0 2px 0 0;
  border-color: var(--accent-color);
  width: 10px;
  height: 10px;
  transform: translateX(var(--switcher-sf-pos-mob) ) rotate(var(--switcher-sf-rotation) );
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 17.5%;
  left: 40px;
}

@media screen and (min-width: 768px) {
  .header__theme-switcher:after {
    width: 14px;
    height: 14px;
    transform: translateX(var(--switcher-sf-pos-tab) ) rotate(var(--switcher-sf-rotation) );
    top: 19%;
  }
}

@media screen and (min-width: 1280px) {
  .header__theme-switcher:after {
    width: 15px;
    height: 15px;
    transform: translateX(var(--switcher-sf-pos-desk) ) rotate(var(--switcher-sf-rotation) );
    top: 19%;
  }
}

