a {
  text-decoration: none;
}

.page-header {
  margin-right: auto;
  margin-left: auto;
  position: relative;

  // min-width: 530px;
  

  @include tablet {
    max-width: 768px;
    height: 60px;
    display: flex;
  }

  @include desktop {
    max-width: 1280px;
    height: 60px;
    display: flex;
  }
}

.main-nav {
  display: flex;
  align-items: center;
  inline-size: fit-content;
  margin: 0 auto;
}

.site-nav {
  display: none;

  @include tablet {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  @include desktop {
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
}

.site-nav__list {
  list-style: none;
  display: inline-flex;
  align-items: center;
}

.site-nav__item:not(:last-child) {
  margin-right: 40px;
}
.site-nav__item:last-child {
   margin-right: 100px;
}

.site-nav__item {
  display: block;

  @include tablet {
    padding-top: 21px;
    padding-bottom: 21px;
  }
}

.site-nav__link {
  color: var(--additional-color);
  cursor: pointer;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @include tablet {
    font-weight: 500;
    font-size: 14px;
    line-height: 1.14;
  }
}

.site-nav__link.current {
  color: var(--accent-color);
}

.site-nav__link:hover,
.site-nav__link:focus {
  color: var(--accent-color);
}

.page-header__link {
  display: flex;
  align-items: center;
}

.page-header__icon {
  width: 32px;
  height: 32px;

  @include desktop {
    margin-left: 2.97px;
  }
}

.page-header__logo {
  display: none;
  color: var(--muted-text-clr);

  @keyframes flicker {

    0%,
    18%,
    22%,
    25%,
    53%,
    57%,
    100% {
      text-shadow: 0 0 4px #ebc3a7, 0 0 11px#ebc3a7, 0 0 19px #ebc3a7, 0 0 40px #f87719,
        0 0 80px #f87719, 0 0 90px#f87719, 0 0 100px#f87719, 0 0 150px #f87719;
    }

    20%,
    24%,
    55% {
      text-shadow: none;
    }
  }

