// ++++++++++++ * THEME SWITCH ++++++++++++++++++++

.header__theme-switcher {
  display: flex;
  align-items: center;
  gap: 14px;
  cursor: pointer;
  width: 38px;
  height: 16px;
  background-color: var(--accent-color);
  background: var( --linear-gradient-down);
  border-radius: 194px;
  padding: 5px;
  position: relative;
  top: 0;
  right: 0;

  @include tablet {
    width: 54px;
    height: 20px;
    margin-left: auto;
    gap: 24px;
  }

  @include desktop {
    width: 60px;
    height: 24px;
    gap: 24px;
  }

  &::before {
    content: '';
    border-radius: 50px;
    background-color: var(--second-color);
    position: absolute;
    top: 12.5%;
    z-index: 2;

    width: 12px;
    height: 12px;
    transform: translateX(var(--switcher-pos-mob));
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include tablet {
      width: 16px;
      height: 16px;
      transform: translateX(var(--switcher-pos-tab));
    }

    @include desktop {
      width: 18px;
      height: 18px;
      transform: translateX(var(--switcher-pos-desk));
    }
  }

  &::after {
    content: '';
    position: absolute;
    top: 17.5%;
    left: 40px;
    z-index: 2;

    border-width: 0 2px 0 0;
    border-radius: 50px;
    border-style: solid;
    border-color: var(--accent-color);
    background-color: transparent;

    width: 10px;
    height: 10px;
    transform: translateX(var(--switcher-sf-pos-mob))
      rotate(var(--switcher-sf-rotation));
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include tablet {
      width: 14px;
      height: 14px;
      top: 19%;
      transform: translateX(var(--switcher-sf-pos-tab))
        rotate(var(--switcher-sf-rotation));
    }

    @include desktop {
      top: 19%;
      width: 15px;
      height: 15px;
      transform: translateX(var(--switcher-sf-pos-desk))
        rotate(var(--switcher-sf-rotation));
    }
  }
}

#icon-moon,
#icon-sun {
  width: 8px;
  height: 8px;

  @include tablet {
    width: 11px;
    height: 11px;
  }
  @include desktop {
    width: 13px;
    height: 13px;
  }
}



